import { Toast, Notify } from 'vant'

export default {
    methods: {
        push(params) {
            this.$router.push(params)
        },
        replace(params) {
            this.$router.replace(params)
        },
        back(num = -1) {
            this.$router.go(num)
        },
        notify(title) {
            Notify(title)
        },
        msg(title) {
            Toast(title)
        },
        sucMsg(title) {
            Toast.success(title)
        },
        errMsg(title) {
            Toast.fail(title)
        },
        loading(title = '加载中...') {
            Toast.loading({
                message: title,
                forbidClick: true
            })
        },
        // 设置背景颜色 - 支持默认颜色和自定义颜色
        setBgColor(bgColor = 'gray') {
            const dom = document.getElementsByTagName('body')[0]
            if (bgColor === 'reset') {
                dom.style.backgroundColor = null
            } else {
                dom.style.backgroundColor = bgColor === 'gray' || !bgColor ? '#f6f6f6' : bgColor
            }
        },
        resetBgColor() {
            this.setBgColor('reset')
        },
        scrollToTop() {
            window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
        }
    }
}
